.my-ride{
    margin:1rem 4rem;
}

.rides{
    width:24rem;
    padding:1rem;
}

.ride-line{
    display:flex;
}

    .ride-line .left{
        width:50%;
        text-align:start;
    }

    .ride-line .right {
        width: 50%;
        text-align:start;
    }

.label {
    color: #a3a1a3;
    font-size:11px;
    font-weight:bolder;
}

.my-ride .head-card .header {
    width: 12rem;
    height: 2.7rem;
    background-color: #ffac19;
    color: white;
    margin:auto 4rem;
    font-size: 1.2rem;
    padding: 0.4rem;
}

.my-ride .rides .head{
    display:flex;
}

.my-ride .rides-cards .no-bookings {
    font-size: 1.5rem;
    margin: 25% auto;
}